'use client';

import { useState, useEffect } from 'react';
import { useAccount, useConnect, useDisconnect } from 'wagmi';
import { injected } from 'wagmi/connectors';
import CivicXChainDashboard from './components/CivicXChainDashboard';

export default function Home() {
  const { address, isConnected } = useAccount();
  const { connect } = useConnect();
  const { disconnect } = useDisconnect();
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    setMounted(true);
  }, []);

  if (!mounted) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-green-50 to-blue-50 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-green-600 mx-auto"></div>
          <p className="mt-4 text-gray-600">Loading CivicXChain...</p>
        </div>
      </div>
    );
  }

  if (!isConnected) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-green-50 to-blue-50 flex items-center justify-center">
        <div className="max-w-md w-full bg-white rounded-xl shadow-lg p-8 text-center">
          <div className="mb-6">
            <h1 className="text-3xl font-bold text-gray-900 mb-2">🌍 CivicXChain</h1>
            <p className="text-gray-600">Environmental Governance on Blockchain</p>
          </div>
          
          <div className="mb-6 p-4 bg-green-50 rounded-lg">
            <h2 className="text-lg font-semibold text-green-800 mb-2">🎯 What You Can Do:</h2>
            <ul className="text-sm text-green-700 space-y-1">
              <li>✅ View real-time environmental commitments</li>
              <li>✅ Create new environmental targets</li>
              <li>✅ Track progress with live oracle data</li>
              <li>✅ Claim CIVIC token rewards</li>
              <li>✅ Monitor blockchain transactions</li>
            </ul>
          </div>

          <div className="mb-6 p-4 bg-blue-50 rounded-lg">
            <h3 className="text-sm font-semibold text-blue-800 mb-2">🔗 Connected to Local Blockchain:</h3>
            <div className="text-xs text-blue-700 space-y-1">
              <p>📊 PM2.5: 12.03 μg/m³ (Live Data)</p>
              <p>🌍 CO2: 416.64 ppm (Live Data)</p>
              <p>🌳 Forest: 65.68% (Live Data)</p>
              <p>💰 100M CIVIC tokens ready</p>
            </div>
          </div>

          <button
            onClick={() => connect({ connector: injected() })}
            className="w-full bg-gradient-to-r from-green-600 to-blue-600 text-white font-semibold py-3 px-6 rounded-lg hover:from-green-700 hover:to-blue-700 transition-all duration-200 shadow-lg hover:shadow-xl"
          >
            🦊 Connect MetaMask to Start
          </button>
          
          <p className="mt-4 text-xs text-gray-500">
            Make sure you're connected to localhost:8545 (Chain ID: 31337)
          </p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-green-50 to-blue-50">
      {/* Header */}
      <header className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            <div className="flex items-center">
              <h1 className="text-2xl font-bold text-gray-900">🌍 CivicXChain</h1>
              <span className="ml-3 px-2 py-1 bg-green-100 text-green-800 text-xs font-medium rounded-full">
                Live on Local Blockchain
              </span>
            </div>
            
            <div className="flex items-center space-x-4">
              <div className="text-sm text-gray-600">
                <span className="font-medium">Connected:</span> {address?.slice(0, 6)}...{address?.slice(-4)}
              </div>
              <button
                onClick={() => disconnect()}
                className="bg-red-100 text-red-700 px-3 py-1 rounded-lg text-sm hover:bg-red-200 transition-colors"
              >
                Disconnect
              </button>
            </div>
          </div>
        </div>
      </header>

      {/* Main Dashboard */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <CivicXChainDashboard />
      </main>
    </div>
  );
}
